import * as React from "react";
import Drawer from "@mui/material/Drawer";
import Toolbar from "@mui/material/Toolbar";
import Box from "@mui/material/Box";
import List from "@mui/material/List";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import HomeIcon from "@mui/icons-material/Home";
import PersonIcon from "@mui/icons-material/Person";
import SubscriptionsIcon from "@mui/icons-material/Subscriptions";
import PublicIcon from "@mui/icons-material/Public";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import LightModeIcon from "@mui/icons-material/LightMode";
import { useNavigate } from "react-router-dom";

export default function PermanentDrawerLeft(props) {
  const drawerWidth = 240;
  const [date, setDate] = React.useState("");
  let dateObj = new Date();
  React.useEffect(() => {
    setInterval(() => {
      setDate((date) => new Date().toLocaleTimeString());
    });
  }, []);
  const navigate = useNavigate();
  const { darkmode, setDarkMode, bannerimg, srcode } = props;
  return (
    <Drawer
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        "& .MuiDrawer-paper": {
          width: drawerWidth,
          boxSizing: "border-box",
        },
      }}
      variant="permanent"
      anchor="left"
    >
      <Toolbar sx={{ p: "0 !important" }}>
        <Box
          component="img"
          sx={{
            objectFit: "cover",
            objectPosition: "center",
            width: "100%",
            height: 100,
          }}
          alt="headericon"
          src={
            bannerimg
              ? bannerimg
              : "https://images.unsplash.com/photo-1512917774080-9991f1c4c750?auto=format&w=350&dpr=2"
          }
        />
      </Toolbar>
      <Divider />
      <List>
        <ListItem disablePadding>
          <ListItemButton onClick={() => navigate("/")}>
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary={"Home"} />
          </ListItemButton>
        </ListItem>

        <ListItem disablePadding>
          <ListItemButton onClick={() => navigate("/members")}>
            <ListItemIcon>
              <PersonIcon />
            </ListItemIcon>
            <ListItemText primary={"Members"} />
          </ListItemButton>
        </ListItem>

        <ListItem disablePadding>
          <ListItemButton onClick={() => navigate("/subscriptions")}>
            <ListItemIcon>
              <SubscriptionsIcon />
            </ListItemIcon>
            <ListItemText primary={"Subscriptions"} />
          </ListItemButton>
        </ListItem>

        <ListItem disablePadding>
          <ListItemButton
            onClick={() => window.open(srcode || "github.com", "_blank")}
          >
            <ListItemIcon>
              <PublicIcon />
            </ListItemIcon>
            <ListItemText primary={"Website"} />
          </ListItemButton>
        </ListItem>

        <ListItem disablePadding>
          <ListItemButton
            onClick={() =>
              setDarkMode((e) => (e === "light" ? "dark" : "light"))
            }
          >
            <ListItemIcon>
              {darkmode === "light" ? <DarkModeIcon /> : <LightModeIcon />}
            </ListItemIcon>
            <ListItemText
              primary={darkmode === "light" ? "Dark Mode" : "Light Mode"}
            />
          </ListItemButton>
        </ListItem>
      </List>
      <List style={{ marginTop: `auto` }}>
        <ListItem>
          <Box sx={{ display: "flex", flexDirection: "column", p: 0 }}>
            <ListItemText>
              <Typography>{date}</Typography>
            </ListItemText>
            <ListItemText>
              <Typography>{`${dateObj.getDate()}/${
                dateObj.getMonth() + 1
              }/${dateObj.getFullYear()}`}</Typography>
            </ListItemText>
          </Box>
        </ListItem>
      </List>
    </Drawer>
  );
}
