import "./styles.css";
import React, { useState } from "react";
import {
  Drawer,
  EditMember,
  EditPrice,
  Home,
  Members,
  Subscriptions,
} from "./components";
import Box from "@mui/material/Box";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { Route, Routes } from "react-router-dom";
import axios from "axios";
import { useLocation } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import LoadingScreen from "react-loading-screen";
// const config = require("./config.json");

export default function App() {
  const locationId = useLocation().pathname.split("/");
  const [loading, setLoading] = useState(true);
  const [darkmode, setDarkMode] = useState("dark");
  const [Data, setData] = useState([]);
  const [Pricing, setPricing] = useState([]);
  const [config, setConfig] = useState({});

  const title = config.REACT_APP_ACADEMY_NAME;
  const url = config.REACT_APP_API_URL || "";
  const bannerimg = config.REACT_APP_ACADEMY_BANNER;
  const srcode = config.REACT_APP_SRC_CODE_URL;

  const darkTheme = createTheme({
    palette: {
      mode: darkmode,
    },
  });

  React.useEffect(() => {
    fetch("config.json").then((response) => {
      response.json().then((settings) => {
        setConfig(settings);
        document.title =
          `${settings.REACT_APP_ACADEMY_NAME} Membership App` ||
          "Membership App";
      });
    });
    axios
      .get(`${url}/account/list`)
      .then((resp) => {
        setData(resp.data);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
    axios
      .get(`${url}/pricing/list`)
      .then((resp) => {
        setPricing(resp.data);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  });

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      {loading ? (
        <LoadingScreen
          loading={loading}
          bgColor="#1a1a1a"
          spinnerColor="#0066ff"
          textColor="#d9d9d9"
          text="Loading"
        />
      ) : (
        <Box sx={{ display: "flex" }} className="App">
          <Drawer
            darkmode={darkmode}
            srcode={srcode}
            bannerimg={bannerimg}
            setDarkMode={setDarkMode}
          />
          <Box
            component="main"
            sx={{ flexGrow: 1, bgcolor: "background.default", p: 3 }}
          >
            <ToastContainer
              position="top-right"
              autoClose={5000}
              hideProgressBar={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              pauseOnHover
              theme={darkmode}
            />
            <Routes>
              <Route path="/" element={<Home title={title} />} />
              <Route
                path="/members"
                element={<Members Data={Data} url={url} />}
              />
              <Route
                path="/subscriptions/"
                element={<Subscriptions Data={Pricing} />}
              />
              <Route
                path="/editprice"
                element={<EditPrice url={url} Data={Pricing} />}
              />
              <Route
                path="/editmembers/:id?"
                element={<EditMember Data={Data} url={url} />}
              />
            </Routes>
          </Box>
        </Box>
      )}
    </ThemeProvider>
  );
}
