import React from "react";
import axios from "axios";
import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import IconButton from "@mui/material/IconButton";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import AddIcon from "@mui/icons-material/Add";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import FilterListIcon from "@mui/icons-material/FilterList";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";

export default function Members(props) {
  const [MenuEl, setMenuEl] = React.useState(null);
  const handleClick = (event) => {
    setMenuEl(event.currentTarget);
  };

  const navigate = useNavigate();
  const { Data, url } = props;
  const [userData, setUserData] = React.useState(Data);
  const Datakeys = Object.keys(userData);
  const [FilteredDatakey, setFilteredDataKey] = React.useState(Datakeys);
  const [search, setSearch] = React.useState("");
  let todayDate = new Date().getTime();
  let oldDate = (index) =>
    new Date(userData[index].fee_history[0].expires_on).getTime();

  const FilteredData = (filterType) => {
    switch (filterType) {
      case "Strength":
        setFilteredDataKey(
          Datakeys.filter(
            (item) => userData[item].fee_history[0].plan === "Strength"
          )
        );
        break;

      case "Cardio":
        setFilteredDataKey(
          Datakeys.filter(
            (item) => userData[item].fee_history[0].plan === "Cardio"
          )
        );
        break;

      case "Cardio,Strength":
        setFilteredDataKey(
          Datakeys.filter(
            (item) => userData[item].fee_history[0].plan === "Cardio,Strength"
          )
        );
        break;

      case "Expired":
        setFilteredDataKey(
          Datakeys.filter((item) => todayDate > oldDate(item))
        );
        break;

      case "Active":
        setFilteredDataKey(
          Datakeys.filter((item) => todayDate < oldDate(item))
        );
        break;
      default:
        setFilteredDataKey(Datakeys);
        break;
    }
  };

  const handleRemoveUser = (key) => (e) => {
    const { [key]: tmp, ...rest } = userData;
    setUserData(rest);
    axios
      .delete(`${url}/account/delete/${key}`)
      .then((res) => {
        toast.success(res.data.msg);
        console.log(res.data);
      })
      .catch((error) => {
        toast.error("oops! an error occurred");
        console.log(error);
      });
  };

  return (
    <div>
      <Box sx={{ display: "flex", p: 4, justifyContent: "space-between" }}>
        <Typography variant="h3">Members</Typography>
        <Box>
          <Button
            sx={{ p: 1.3, mr: 2, textAlign: "center" }}
            variant="outlined"
            onClick={() => navigate("/editmembers")}
          >
            <AddIcon />
            New Member
          </Button>
          <IconButton onClick={handleClick}>
            <FilterListIcon />
          </IconButton>
          <Menu
            id="filter-menu"
            anchorEl={MenuEl}
            open={Boolean(MenuEl)}
            onClose={() => setMenuEl(null)}
            MenuListProps={{
              "aria-labelledby": "basic-button"
            }}
          >
            <Typography sx={{ m: 1 }}>Filter by : </Typography>
            <MenuItem onClick={(i) => FilteredData("none")}>None</MenuItem>
            <MenuItem onClick={(i) => FilteredData("Active")}>Active</MenuItem>
            <MenuItem onClick={(i) => FilteredData("Expired")}>
              Expired
            </MenuItem>
            <MenuItem onClick={(i) => FilteredData("Cardio")}>Cardio</MenuItem>
            <MenuItem onClick={(i) => FilteredData("Strength")}>
              Strength
            </MenuItem>
            <MenuItem onClick={(i) => FilteredData("Cardio,Strength")}>
              Cardio,Strength
            </MenuItem>
          </Menu>
          <TextField
            placeholder="Name"
            id="outlined-basic"
            label="Search"
            variant="outlined"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </Box>
      </Box>

      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="center">Name</TableCell>
              <TableCell align="center">Age</TableCell>
              <TableCell align="center">Mobile </TableCell>
              <TableCell align="center">Date of join</TableCell>
              <TableCell align="center">Register Number</TableCell>
              <TableCell align="center">Current Plan</TableCell>
              <TableCell align="center">Plan Status</TableCell>
              <TableCell align="center">Fees Due</TableCell>
              <TableCell align="center">Edit</TableCell>
              <TableCell align="center">Delete</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {FilteredDatakey.filter((e) =>
              userData[e]?.name?.toLowerCase().includes(search)
            ).map((row, i) => (
              <TableRow
                key={userData[row].name}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                <TableCell>{i + 1}</TableCell>
                <TableCell align="center">{userData[row].name}</TableCell>
                <TableCell align="center">{userData[row].age}</TableCell>
                <TableCell align="center">
                  {userData[row].mobile_number}
                </TableCell>
                <TableCell align="center">
                  {userData[row].date_of_join}
                </TableCell>
                <TableCell align="center">
                  {userData[row].register_number}
                </TableCell>
                <TableCell align="center">
                  {userData[row].fee_history[0].plan}
                </TableCell>
                <TableCell align="center">
                  {todayDate > oldDate(row) ? (
                    <Typography color="error">Expired</Typography>
                  ) : (
                    <Typography color="success.main">Active</Typography>
                  )}
                </TableCell>

                <TableCell align="center">{userData[row].fees_due}</TableCell>
                <TableCell align="center">
                  <IconButton onClick={() => navigate(`/editmembers/${row}`)}>
                    <EditIcon />
                  </IconButton>
                </TableCell>
                <TableCell align="center">
                  {" "}
                  <IconButton color="error" onClick={handleRemoveUser(row)}>
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
