import React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import { useLocation } from "react-router-dom";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import IconButton from "@mui/material/IconButton";
import axios from "axios";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { toast } from "react-toastify";

export default function EditMember(props) {
  const id = useLocation().pathname.split("/");
  const { Data, url } = props;
  const [userData, setUserData] = React.useState({
    name: "",
    age: 0,
    date_of_join: "",
    mobile_number: "",
    register_number: "",
    fees_due: 0,
    aadhar_number: "",
    address: "",
    fee_history: [
      {
        plan: "",
        paid_on: "",
        expires_on: "",
        amount: 0
      }
    ]
  });
  const Datakeys = Object.keys(userData);

  React.useState(() => {
    if (id[2] !== undefined) {
      const newData = Data[id[2]];
      setUserData(newData);
    }
  }, []);
  const handleSubmit = (event) => {
    event.preventDefault();
    if (id[2] !== undefined) {
      //edit exsist account
      axios
        .put(`${url}/account/${id[2]}`, userData)
        .then((res) => {
          toast.success(res.data.msg);
          console.log(res.data);
        })
        .catch((error) => {
          toast.error("oops! an error occurred");
          console.log(error);
        });
    } else {
      //new account
      axios
        .post(`${url}/account/addaccount`, userData)
        .then((res) => {
          toast.success(res.data.msg);
          console.log(res.data);
        })
        .catch((error) => {
          toast.error("oops! an error occurred");
          console.log(error);
        });
    }
  };
  function handleChange(evt) {
    const value = evt.target.value;
    setUserData({
      ...userData,
      [evt.target.name]: value
    });
  }

  const handleFeeChange = (index) => (e) => {
    const newArray = userData.fee_history.map((item, i) => {
      if (index === i) {
        return { ...item, [e.target.name]: e.target.value };
      } else {
        return item;
      }
    });
    setUserData({ ...userData, fee_history: newArray });
  };
  const handleNewFee = () => {
    const obj = {
      plan: "",
      date: "",
      amount: 0
    };
    let newArray = userData.fee_history;
    newArray.unshift(obj);
    setUserData({ ...userData, fee_history: newArray });
  };
  const handleRemoveFee = (i) => (e) => {
    const updatedFee = userData.fee_history.filter(
      (item, index) => index !== i
    );
    setUserData({ ...userData, fee_history: updatedFee });
  };

  return (
    <Box
      sx={{
        marginTop: 8,
        display: "flex",
        flexDirection: "column",
        alignItems: "center"
      }}
    >
      <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
        <LockOutlinedIcon />
      </Avatar>
      <Typography component="h1" variant="h5">
        Add/Edit Members
      </Typography>
      <Box component="form" sx={{ mt: 3 }}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              autoComplete="name"
              name="name"
              required
              fullWidth
              label="Name"
              value={userData.name}
              autoFocus
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              type="number"
              label="Age"
              name="age"
              value={userData.age}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              type="date"
              label="Date of Joined"
              name="date_of_join"
              value={userData.date_of_join}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              label="Register Number"
              name="register_number"
              value={userData.register_number}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              name="mobile_number"
              label="Mobile"
              type="mobile"
              value={userData.mobile_number}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              name="aadhar_number"
              label="Aadhar Number"
              value={userData.aadhar_number}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              name="address"
              label="Address"
              value={userData.address}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              required
              fullWidth
              name="fees_due"
              label="Fees Due"
              type="number"
              value={userData.fees_due}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <Typography variant="h4">
              Fees History{" "}
              <IconButton onClick={handleNewFee}>
                <AddIcon />
              </IconButton>
            </Typography>
          </Grid>
          {userData.fee_history.map((e, i) => (
            <React.Fragment key={i}>
              <Grid item xs={2}>
                <FormControl fullWidth>
                  <InputLabel>Plan</InputLabel>
                  <Select
                    name="plan"
                    value={e.plan}
                    label="Plan"
                    onChange={handleFeeChange(i)}
                  >
                    <MenuItem value={"Cardio"}>Cardio</MenuItem>
                    <MenuItem value={"Strength"}>Strength</MenuItem>
                    <MenuItem value={"Cardio,Strength"}>
                      Cardio,Strength
                    </MenuItem>
                    <MenuItem value={"Others"}>Other</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={2}>
                <TextField
                  required
                  fullWidth
                  name="paid_on"
                  type="date"
                  label="Paid on"
                  value={e.paid_on}
                  onChange={handleFeeChange(i)}
                />
              </Grid>
              <Grid item xs={2}>
                <TextField
                  required
                  fullWidth
                  name="expires_on"
                  type="date"
                  label="Expires on"
                  value={e.expires_on}
                  onChange={handleFeeChange(i)}
                />
              </Grid>
              <Grid item xs={2}>
                <TextField
                  required
                  fullWidth
                  name="amount"
                  label="Amount"
                  value={e.amount}
                  onChange={handleFeeChange(i)}
                />
              </Grid>
              <Grid item xs={3}>
                <IconButton onClick={handleRemoveFee(i)} color="error">
                  <DeleteIcon />
                </IconButton>
              </Grid>
            </React.Fragment>
          ))}
        </Grid>
        <Button
          onClick={handleSubmit}
          variant="contained"
          sx={{ mt: 3, mb: 2 }}
        >
          Save
        </Button>
      </Box>
    </Box>
  );
}
