import React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardHeader from "@mui/material/CardHeader";
import StarIcon from "@mui/icons-material/StarBorder";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";
import axios from 'axios'
import Fab from '@mui/material/Fab';
import EditIcon from "@mui/icons-material/Edit";
import { useNavigate } from "react-router-dom";
import Container from "@mui/material/Container"
import Grid from "@mui/material/Grid"
import DownloadIcon from '@mui/icons-material/Download';
import Stack from '@mui/material/Stack';

export default function Subscriptions(props) {
  const {Data} = props
  const [PriceData, setPriceData] = React.useState(Data);
   const Datakeys = Object.keys(Data)
  const navigate = useNavigate();
 
  return (
    <>
    <div id="Pricing">
      <Container
        disableGutters
        maxWidth="sm"
        component="main"
        sx={{ pt: 5, pb: 6 }}
        
      >
        <Typography
          component="h1"
          variant="h2"
          align="center"
          color="text.primary"
          gutterBottom
        >
          {PriceData[0].title} 
        </Typography>
        <Typography
          variant="h5"
          align="center"
          color="text.secondary"
          component="p"
        >
          {PriceData[0].description}
        </Typography>
      </Container>
      {/* End hero unit */}
      <Container maxWidth="md" component="main">
        <Grid container spacing={5} alignItems="flex-end">
          {Datakeys.filter(e=>e !="0").map((key) => (
            // Enterprise card is full width at sm breakpoint
            <Grid
              item
              key={key}
              xs={12}
              sm={6}
              md={4}
            >
              <Card>
                <CardHeader
                  title={PriceData[key].title}
                  subheader={PriceData[key].subheader}
                  titleTypographyProps={{ align: "center" }}
                  
                  subheaderTypographyProps={{
                    align: "center"
                  }}
                  sx={{
                    backgroundColor: (theme) =>
                      theme.palette.mode === "light"
                        ? theme.palette.grey[200]
                        : theme.palette.grey[700]
                  }}
                />
                <CardContent>
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "baseline",
                      mb: 2
                    }}
                  >
                    <Typography
                      component="h2"
                      variant="h3"
                      color="text.primary"
                    >
                      ${PriceData[key].price}
                    </Typography>
                    <Typography variant="h6" color="text.secondary">
                      /mo
                    </Typography>
                  </Box>
                  <ul>
                    {PriceData[key].description.map((line) => (
                      <Typography
                        component="li"
                        variant="subtitle1"
                        align="center"
                        key={line}
                      >
                        {line}
                      </Typography>
                    ))}
                  </ul>
                </CardContent>
                <CardActions>
                  <Button fullWidth variant={PriceData[key].buttonVariant}>
                    {PriceData[key].buttonText}
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
        
      </Container>
    </div>
    <Box sx={{  display:"flex",  flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
}}>
        <Stack sx={{position: 'fixed',
    bottom: 10,
    right: 10,
}} direction="row" spacing={2}>
          <Fab size="medium" color="secondary" 
                      onClick={() =>
                        navigate(`/editprice/`)
                      }
                    >
                      <EditIcon />
                    </Fab>
          
        </Stack>
          </Box>
    </>
  );
}
