import React from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import axios from "axios";
import { toast } from "react-toastify";

export default function EditPrice(props) {
  const { Data, url } = props;
  const [PriceData, setPriceData] = React.useState(Data);

  const handleSubmit = (event) => {
    event.preventDefault();
    axios
      .post(`${url}/pricing/edit`, PriceData)
      .then((res) => {
        toast.success(res.data.msg);
        console.log(res.data);
      })
      .catch((error) => {
        toast.error("oops! an error occurred");
        console.log(error);
      });
  };

  return (
    <div>
      <TextField
        value={JSON.stringify(PriceData, undefined, 2)}
        multiline
        sx={{ width: "100%" }}
        onChange={(e) => setPriceData(JSON.parse(e.target.value))}
      />
      <Button onClick={handleSubmit} variant="contained" sx={{ mt: 3, mb: 2 }}>
        Save
      </Button>
    </div>
  );
}
